  --black-fogra: #0A1E27;
  --dark-black-fogra: #051116;
  --bright-yellow: #FCA61D;
  --princeton-orange: #F5812A;
  --cinnabar: #E64D2D;
  --floral-white: #FFFCF7;
}

/* List Style */
ul{
  list-style: none;
}

/* Buttons */
button{
  outline: none;
  border: none;
}
