/* Make site dark when popup is active */
.popupBox__bg{
  z-index: 30;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, .4);
  transition: .5s;
}
.popupBox{
  z-index: -10;
  opacity: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  height: 90%;
  width: 80%;
  max-width: 750px;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  transition: .5s;
}
.popupBox-active{
  opacity: 1;
  z-index: 40;
  transition: 0s;
}

/* Top and Bottom of popup */
.popupBox aside{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}
.popupBox__top{
  top: 0;
  height: 70%;
  color: var(--black-fogra);
  box-shadow: 0 20px 80px 0 rgb(0 0 0 / 55%);
  background: var(--floral-white);
  -webkit-transform: translateX(100vw);
          transform: translateX(100vw);
  transition: .5s;

  border: solid 10px var(--black-fogra);
  border-bottom: none;
}
.popupBox__top-active{
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.popupBox__bottom{
  bottom: 0;
  height: 30%;
  color: var(--floral-white);
  box-shadow: 0 80px 80px 0 rgb(0 0 0 / 55%);
  background: var(--black-fogra);
  -webkit-transform: translateX(-100vw);
          transform: translateX(-100vw);
  transition: .5s;

  border: solid 10px var(--black-fogra);
  border-top: none;
}
.popupBox__bottom-active{
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

/* Box inside aside tags */
.popupBox__content{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 80%;
}
.popupBox__content h3{
  font-weight: 600;
  font-size: 1.5rem;
}
.popupBox__content small{
  margin: 5px 0 15px 0;
  font-size: .8rem;
}
.popupBox__content p,
.popupBox__content label{
  font-size: 1rem;
  font-weight: 500;
}
.popupBox__content a{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 15px;
  color: var(--black-fogra);
  border-radius: 5px;
  background: var(--bright-yellow);
  transition: .2s;
}
.popupBox__content a:hover{
  background: var(--cinnabar);
}
.popupBox__content a i{
  font-size: inherit;
  margin-left: 5px;
}

/* Closing X */
.popupBox__close{
  z-index: 45;
  position: absolute;
  top: 20px;
  right: 30px;
  height: 25px;
  width: 25px;
  cursor: pointer;
}
.popupBox__close::before{
  content: '';
  position: absolute;
  top: 20px;
  height: 3px;
  width: 30px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  border-radius: 5px;
  background: var(--black-fogra);
}
.popupBox__close::after{
  content: '';
  position: absolute;
  top: 20px;
  height: 3px;
  width: 30px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  border-radius: 5px;
  background: var(--black-fogra);
}

/* About */
.popupBox ul{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
  padding: 25px 0 10px 0;
}
.popupBox i{
  font-size: 3rem;
  transition: .2s;
}
.popupBox li:nth-child(1):hover i{
  color: #F75421;
}
.popupBox li:nth-child(2):hover i{
  color: #2091EB;
}
.popupBox li:nth-child(3):hover i{
  color: #EFD81D;
}
.popupBox li:nth-child(4):hover i{
  color: #5ED3F3;
}
.popupBox li:nth-child(5):hover i{
  color: #09C980;
}


/* Tablet */
@media screen and (max-width: 768px){
  .popupBox{
    height: 90%;
    width: 90%;
  }
  .popupBox__content h3{
    font-size: 1.3rem;
  }
  .popupBox__content small{
    font-size: .7rem;
    margin: 5px 0 10px 0;
  }
  .popupBox__content p,
  .popupBox__content label,
  .popupBox__content a{
    font-size: .9rem;
  }
  .popupBox__close{
    top: 20px;
    right: 20px;
  }
  .popupBox__close::before{
    width: 20px;
  }
  .popupBox__close::after{
    width: 20px;
  }
  .popupBox i{
    font-size: 3rem;
  }
}
/* Mobile L */
@media screen and (max-width: 425px){
  .popupBox{
    margin-left: -2.5%;
  }
  .popupBox__content h3{
    font-size: 1.1rem;
  }
  .popupBox__content small{
    font-size: .7rem;
